body {
  --header-height: 60px;
  --active-link-color: #00acdc;
  background-color: #000;
  margin: 0;
  font-family: Roboto, serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list-item:not(:first-child) {
  margin-left: 20px;
}

.link {
  height: var(--header-height);
  line-height: var(--header-height);
  white-space: nowrap;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
  position: relative;
}

.link:hover, .is-active {
  color: var(--active-link-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
